.Comments {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-left: 40px;
}
.Comment {
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	margin-top: 15px;
	margin-bottom: 15px;
	line-height: 21px;
	font: normal normal 400 normal 14px "Noto Sans", sans-serif;
	text-align: left;
}
.gilding {
	margin: 0 3px;
}
.gilding img {
	vertical-align: middle;
}
.left-box {
	margin: 3px 0 0 0;
}
.Comment p {
	padding-bottom: 3.5px;
	padding-left: 0;
	padding-right: 0;
	padding-top: 0;
	margin: 0;
}
.Comment * {
	line-height: 21px;
	word-break: break-word;
}
.controls {
	display: flex;
	justify-content: space-between;
	margin: 25px 0;
}
.controls button {
	font-size: 24px;
	padding: 5px 10px;
}
.highlight-danger {
	color: rgb(233, 14, 14);
	text-decoration: underline;
}
.highlight-warn {
	font-weight: 700;
	color: black;
}
body {
	overflow-y: scroll;
}